<template>
    
</template>

<script>

export default {
    computed:{
        waitKit(){
            if (this.$store.state.initWithKit) {
                this.$router.push('/');
            }
            else {
                setTimeout(this.waitKit, 10);
            }
        }
    },
    props:['id'],
    async mounted(){
        await fetch('http://banana-cobbler-95780.herokuapp.com/api/kit/'+this.id) //don't forget to change the url while dev/prod https://127.0.0.1:8000/
        .then(response => response.json())
        .then(data => {
            var data = JSON.parse(data.samples);
            var sampleList = [];
            data.forEach(sample => {
                sample.fields.file="https://vlmboiteason.s3.amazonaws.com/static/media/"+sample.fields.file;
                sampleList.push(sample.fields);
            });
            this.$store.commit('initKit',sampleList);
        });
        this.waitKit;
    }
}
</script>